*{
  margin: 0;
  padding: 0;
}
html, body{
  height: 100%;
}
body{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
  background-color: #faa60b;
  display: flex;
  justify-content: center;
  align-items: center;
}
h1{
  color: #025a75;
  text-align: center;
  margin: 2em 0;
  border: 5px double #025a75;
  border-radius: 11px;
}
.container{
  height: 850px;
  width: 750px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 1em;
}
.card{
  background-color: rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 13px;
  transform: rotateY(180deg);
  animation: 2s hideCard linear;
  transition: transform 0.5s;
}
.card:hover{
  cursor: pointer;
}
.card img{
  max-width: 80%;
  max-height: 80%;
  transition: transform 0.5s;
  transform: scale(0);
  animation: 2s hideImage linear;
}
@keyframes hideCard{
  0%, 70%{
    transform: rotateY(0);
  }
  100%{
    transform: rotateY(180deg);
  }
}
@keyframes hideImage{
  0%, 70%{
    transform: scale(1);
  }
  100%{
    transform: scale(0);
  }
}
.card.active{
  transform: rotateY(0);
}
.card.active img{
  transform: scale(1);
}
@media (max-width: 1024px){
  h1{
    margin: -3rem 4rem 4rem 4rem;
  }
  .container{
    height: 500px;
    width: 375px;
    margin: 0 0.5rem 0 0.5rem;
    gap: 0.5rem;
  }
}